/**
  *  -    .
  * @function
  * @param  {Number} min 
  * @param  {Number} max 
  * @param  {Number} precesion 
  * @return {Number}  -   
  */
export function RandomNum (
  min = 0,
  max = 100,
  precesion = 0
): number {
  let range = max - min
  let n = Math.random() * range + min
  return Number(n.toFixed(precesion))
}

