/**
   * transform  merging  Object    .
   * CSS Transform    transofrm function .
   *  css transform  merging  .    .
   *
   * @param {Object} transform { rotate: 30, translateX: -10 }  transform object .
   * @return {Object} { transform: rotate(30deg) translateX(-10px) }  .
   */
export function GetTransformStyle (
  transform = {}
): { [key: string]: string } {
  let styleArr: string[] = []
  const units: { [key: string]: string } = {
    'rotate': 'deg',
    'translateX': 'px',
    'translateY': 'px'
  }
  for (let [key, val] of Object.entries(transform)) {
    if (units[key] && val !== 0) {
      val = val + units[key]
    }
    styleArr.push(`${key}(${val})`)
  }

  return {
    transform: styleArr.join(' ')
  }
}

/**
 *  val  Array .
 * @function
 * @param {number} length Array 
 * @param {*} val  value
 */
export function ArrayFrom (
  length: number = 1,
  val: any = null
): any[] {
  return Array.from(Array(length)).map(item => val)
}
