














































import { Component, Prop, Vue } from 'vue-property-decorator'
import { CreateElement, VNode } from 'vue'
import { GetTransformStyle } from './utils'

/**
 *      .
 *
 * @export
 * @interface IParticleShape
 * @property {Number} shape    (3 - 6)
 * @property {String} cls   css class
 * @property {top: string, left: string} position    css position
 * @property {Number} scale   . 1 = 100%
 * @property {Number} parallaxScale      
 * @property {Number} animationDuration   
 */
export interface IParticleShape {
  shape: number,
  cls?: string,
  position?: { [key: string]: string },
  scale?: number,
  rotate?: number,
  parallaxScale?: number,
  animationDuration?: number
}

@Component
export default class ParticleShape extends Vue {
  /**
   *    IParticleShape .
   */
  @Prop({ default () {
    return {
      shape: 3, cls: '-tri', styl: {}, scale: 1, parallaxScale: 0
    }
  } })
  readonly shape!: IParticleShape

  /**
   * Parallax       scrollY   .
   */
  @Prop({ default: 0 })
  parallaxBase!: number

  /**
   *   transform  .
   * @return {Object} CSS Transform  .
   */
  get sliceTransform (): { [key: string]: string } {
    // this.parallaxBase * this.
    let transform = {
      'rotate': this.shape.rotate
    }
    return GetTransformStyle(transform)
  }

  /**
   *    div transform  .
   * @return {Object} CSS Transform  .
   */
  get shapeTransform (): { [key: string]: string } {
    let scaleStyle = {
      scale: this.shape.scale
    }
    let parallaxStyle
    if (this.shape.parallaxScale) {
      parallaxStyle = {
        translateY: this.parallaxBase * this.shape.parallaxScale,
        translateZ: 0
      }
    }
    return GetTransformStyle({ ...scaleStyle, ...parallaxStyle })
  }

  /**
   *     ,   .
   * @return {Object} CSS Transform  .
   */
  get animation (): { [key: string]: string } {
    return {
      'animation-duration': this.shape.animationDuration + 's',
      'animation-direction': Math.round(Math.random()) ? 'normal' : 'reverse'
    }
  }

  /**
   * transform  merging  Object    .
   * CSS Transform    transofrm function .
   *  css transform  merging  .    .
   *
   * @param {Object} transform { rotate: 30, translateX: -10 }  transform object .
   * @return {Object} { transform: rotate(30deg) translateX(-10px) }  .
   */
  /* getTransformStyle (
    transform:{ [key: string]: any } = {}
  ): { [key: string]: string } {
