    const units: { [key: string]: string } = {
      'rotate': 'deg',
      'translateX': 'px',
      'translateY': 'px'
    }
    for (let [key, val] of Object.entries(transform)) {
      if (units[key] && val !== 0) {
        val = val + units[key]
      }
      styleArr.push(`${key}(${val})`)
    }

    return {
      transform: styleArr.join(' ')
    }
  } */
}
