











import { Component, Prop, Vue } from 'vue-property-decorator'
import { CreateElement, VNode } from 'vue'
import ParticleShape, { IParticleShape } from './ParticleShape.vue'

/**
*   .
* shape: 3 ~ 6  . 3 - 6
* cls:    css class
* count:    .
*
* @export
* @interface IParticle
*/
export interface IParticle {
  shape: number,
  cls: string,
  count: number,
  scaleMax: number,
  scaleMin: number,
  animationMin?: number,
  animationMax?: number
  rotate?: boolean,
}

@Component({
  components: {
    'particle-shape': ParticleShape
  }
})
export default class TheParticles extends Vue {
  /**
   *   .
   *
   * @readonly
   * @type {IParticle[]}   Array .
   * @memberof IParticle
   */
  @Prop({ default () {
    return [ { shape: 3, cls: '-tri', count: 15, rotate: true } ]
  } })
  readonly particles!: IParticle[]

  /**
   * parallax scroll   .
   * @readonly
   */
  @Prop({ default: false })
  readonly useParallax!: boolean

  mounted () {
    if (this.useParallax) {
      window.addEventListener('scroll', this.handleScroll)
      this.scrollY = window.scrollY
    }
  }

  beforeDestroy () {
    if (this.useParallax) window.removeEventListener('scroll', this.handleScroll)
  }

  /**
   * @function
   * window scrollY   this.scrollY .
   */
  handleScroll (e: Event): void {
    this.scrollY = window.scrollY
  }

  /**
   *  -    .
   * @function
   * @param  {Number} min 
   * @param  {Number} max 
   * @param  {Number} precesion 
   * @return {Number}  -   
   */
  randomNum (min = 0, max = 100, precesion = 0): number {
    let range = max - min
    let n = Math.random() * range + min
    return Number(n.toFixed(precesion))
  }

  /**
   * @function
   * @param {IParticle} particle    .
   * @return {Object} style object .
   */
  particlePosition (particle: IParticle): { [key: string]: string } {
    let baseStyle = {
      'top': this.randomNum(-50, 150) + '%',
      'left': this.randomNum() + '%'
    }
    return baseStyle
  }

  /**
   * @function
   * @return {IParticleShape[]}   Interface Array .
   */
  get particlesArr (): IParticleShape[] {
    let result: IParticleShape[] = []
    return this.particles.reduce((acc, particle) => {
      let arr: IParticleShape[] = []
      for (let i = 0; i < particle.count; i++) {
        let shape:IParticleShape = {
          ...particle,
          scale: this.randomNum(particle.scaleMin, particle.scaleMax, 1),
          position: this.particlePosition(particle),
          rotate: particle.rotate === false ? 0 : this.randomNum(0, 180),
          parallaxScale: this.randomNum(0.7, 2.5, 2) * -1,
          animationDuration: this.randomNum(particle.animationMin, particle.animationMax)
        }
        arr.push({ ...shape })
      }
      return [...acc, ...arr]
    }, result)
  }

  scrollY:number = 0
}
